<template>
<div class="container">
    <h2>Таблица рекордов🏆</h2>  
    <div class="easy">
        <h3>Легкий уровень</h3>
        <ul>
            <li v-for="(player, index) in easyPlayers" :key="index">
                {{ player.name }} - {{ player.time }} сек.
            </li>
        </ul>
    </div>
    <div class="medium">
        <h3>Средний уровень</h3>
        <ul>
            <li v-for="(player, index) in mediumPlayers" :key="index">
                {{ player.name }} - {{ player.time }} сек.
            </li>
        </ul>
    </div>

    <div class="hard">
        <h3>Сложный уровень</h3>
        <ul>
            <li v-for="(player, index) in hardPlayers" :key="index">
                {{ player.name }} - {{ player.time }} сек.
            </li>
        </ul>
    </div>
</div>
<button class="main-button" @click="goToMain">На главную</button> <!-- Кнопка для перехода на главную -->
</template>

<script>
import { computed, onMounted } from 'vue';
import { useLeaderboardStore } from '../stores/leaderboard';

export default {
  
  name: 'LeaderboardComponent',
  setup() {
    const store = useLeaderboardStore();
    const players = computed(() => store.players);

    // Вычисляемые свойства для фильтрации игроков по уровню
    const easyPlayers = computed(() => players.value.filter(player => player.level === 'easy'));
    const mediumPlayers = computed(() => players.value.filter(player => player.level === 'medium'));
    const hardPlayers = computed(() => players.value.filter(player => player.level === 'hard'));

    onMounted(() => {
      store.loadPlayers(); // Загружаем игроков из Local Storage при монтировании компонента
    });

    return {
      easyPlayers,
      mediumPlayers,
      hardPlayers,
    };
  },
  methods: {
    goToMain() {
      this.$router.push('/'); // Переход на главную страницу
    }
  }
};
</script>

<style scoped>
.easy {
    background-color: #d4edda; 
    border: 1px solid #28a745; 
    padding: 10px; 
    margin-bottom: 10px;     
}

.medium {
    background-color: #fff3cd; 
    border: 1px solid #ffc107;
    padding: 10px; 
    margin-bottom: 10px;     
}

.hard {
    background-color: #f8d7da; 
    border: 1px solid #dc3545; 
    padding: 10px; 
    margin-bottom: 10px;    
}
/* Основные стили контейнера */
.container {
    max-width: 600px; 
    margin: 0 auto; 
    padding: 20px; 
    background-color: #f9f9f9; 
    border-radius: 8px; 
    box-shadow: 3px 4px 10px 3px rgba(0, 0, 0, 0.2); 
    margin-top: 10px;
    font-family: 'Times New Roman', Times, serif; 
}

/* Заголовок таблицы */
h2 {
    text-align: center; 
    color: #2c3e50; 
    margin: 10px 0; 
    font-size: 1.5em; 
    font-weight: 400;    
    text-transform: capitalize;
    padding: 10px; 
    box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.1); 
}
h3 {
    color: #218838;
    font-size: 1.1em;   
}
/* Стили для списков */
ul {
    list-style-type: none; 
    padding: 0; 
}

/* Стили для элементов списка */
li {
    background-color: #fff; 
    border: 1px solid #ddd; 
    border-radius: 4px; 
    padding: 10px; 
    margin: 5px 0;
    transition: background-color 0.3s; 
}
.main-button {
    display: block; 
    margin: 20px auto;
    padding: 10px 20px; 
    font-size: 1em; 
    color: white; 
    background-color: #28a745; 
    border: none; 
    border-radius: 5px; 
    cursor: pointer; 
    transition: background-color 0.3s; 
}

.main-button:hover {
    background-color: #218838; 
    box-shadow: 0 0 0 2px white, 0 0 0 4px #218838;
}
</style>