// src/router/index.js
import { createRouter, createWebHashHistory } from 'vue-router';
import GameComponent from '../components/GameComponent.vue'; // Убедитесь, что путь правильный
import SettingsComponent from '../components/SettingsComponent.vue'; // Убедитесь, что путь правильный
import LeaderboardComponent from '../components/LeaderboardComponent.vue';

const routes = [
  {
    path: '/',
    name: 'Home',
    component: GameComponent, // Это может быть ваш главный компонент
  },
  {
    path: '/game',
    name: 'Game',
    component: GameComponent, // Компонент для игры
  },
  {
    path: '/settings',
    name: 'Settings',
    component: SettingsComponent, // Компонент для настроек
  },
  {
    path: '/leaderboard',
    name: 'Leaderboard',
    component: LeaderboardComponent,
  },
];

const router = createRouter({
  history: createWebHashHistory(), // Изменено на hash
  routes,
});

export default router;