<template>
  <div class="settings">
    <h1>Настройки игры🛠️</h1>
    <div>
      <label class= "easy">
        <input type="radio"  value="easy" v-model="selectedDifficulty" />
        Простой 8x8, 10 мин
      </label>
      <label class= "medium">
        <input type="radio"  value="medium" v-model="selectedDifficulty" />
        Средний 16x16, 40 мин
      </label>
      <label class= "hard">
        <input type="radio"  value="hard" v-model="selectedDifficulty" />
        Сложный 32x16, 100 мин
      </label>
    </div>
    <button class="btnStart" @click="startGame">Начать игру</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selectedDifficulty: 'easy', // Значение по умолчанию
    };
  },
  methods: {
    startGame() {
      // Переход к игре с параметрами сложности
      this.$router.push({
        path: '/game',
        query: { difficulty: this.selectedDifficulty }
      });
    },
  },
};
</script>

<style scoped>
