// Импортируем функцию defineStore из библиотеки Pinia для создания хранилища
import { defineStore } from 'pinia';

// Определяем хранилище для таблицы лидеров с именем 'leaderboard'
export const useLeaderboardStore = defineStore('leaderboard', {
  // Определяем состояние хранилища
  state: () => ({
    players: [], // Массив для хранения информации о игроках
  }),
  actions: {
    // Метод для загрузки игроков из localStorage
    loadPlayers() {
      // Получаем строку данных из localStorage по ключу 'leaderboard'
      const storedPlayers = localStorage.getItem('leaderboard');
      
      // Если данные существуют, парсим их из JSON-формата
      if (storedPlayers) {
        this.players = JSON.parse(storedPlayers); // Присваиваем массиву players загруженные данные
      }
    },
    
    // Метод для добавления нового игрока в таблицу лидеров
    addPlayer(player) {
      // Добавляем нового игрока в массив players
      this.players.push(player);
      
      // Сортируем массив игроков по времени (time) в порядке возрастания
      this.players.sort((a, b) => a.time - b.time);
      
      // Обрезаем массив до 10 лучших игроков
      this.players = this.players.slice(0, 10);
      
      // Сохраняем обновленный массив игроков в localStorage в формате JSON
      localStorage.setItem('leaderboard', JSON.stringify(this.players));
    },
  },
});
