import { createApp } from 'vue';
import { createPinia } from 'pinia';
import App from './App.vue';
import router from './router'; // Импортируйте маршрутизатор
import './styles.css'; // Импортируем глобальный CSS файл

const app = createApp(App);
const pinia = createPinia(); // Создайте экземпляр Pinia

app
  .use(pinia) // Подключите Pinia
  .use(router) // Подключите маршрутизатор
  .mount('#app'); // Смонтируйте приложение
