<template>
 <div>
    <div class="remaining-mines"><span class="remaining-mines-icon">⚠️</span> Осталось мин: {{ remainingMines }}</div>  
    
</div>
<div class="navigation__block">
    <button class="navigation__button" @click="restartGame">🔄</button>
    <router-link class="table__button" to="/leaderboard">Таблица лидеров</router-link>   
    <button class="navigation__button" @click="goToSettings">🛠️</button>      
     
 </div>
 
  <div class="game">
    <div class="timer"><span class="timer-icon"><div class="clock">
    <div class="hand"></div>
</div> </span>  <span class="timer-value">{{ displayTimer }}</span></div>  
    <div>
      
      <div class="grid" :style="gridStyle">
        <div 
          v-for="cell in cells" 
          :key="cell.id" 
          :class="['cell', { opened: cell.isOpened, mine: cell.isMine }]" 
          @click="handleCellClick(cell)" 
          @contextmenu.prevent="handleRightClick(cell)"
        >
          <span v-if="cell.isOpened">
            <span v-if="cell.isMine">💣</span>
            <span v-else :class="getNumberClass(cell.value)">{{ cell.value }}</span>
          </span>
          <span v-if="!cell.isOpened && cell.flagged === 'flag'">🚩</span>
          <span v-if="!cell.isOpened && cell.flagged === 'question'">❓</span>
        </div>
      </div>
    </div>
  </div>
 
</template>

<script>
import { useLeaderboardStore } from '../stores/leaderboard'; // Импортируем хранилище для таблицы лидеров

export default {
  data() {
    return {
      timer: 0, // Таймер для отслеживания времени игры
      remainingMines: 10, // Количество оставшихся мин
      cells: [], // Массив ячеек игрового поля
      timerInterval: null, // Переменная для хранения интервала таймера
      rows: 8, // Количество строк в игровом поле
      cols: 8, // Количество столбцов в игровом поле
      difficulty: 'easy', // Уровень сложности по умолчанию
      windowWidth: window.innerWidth, // Ширина окна браузера для адаптивного дизайна
      
      };
  },
  
  computed: {
    displayTimer() {
    return (this.timer !== undefined && !isNaN(this.timer)) ? this.timer : 0;
  },
  gridStyle() {
      const width = this.windowWidth; // Получаем ширину экрана
  // Определяем размер ячеек в зависимости от ширины экрана
  let cellSize;
  if (width < 340) {
    cellSize = 17; // Для маленьких экранов
  } else if (width < 400) {
    cellSize = 19; // Для средних экранов
  } 
  else if (width < 480) {
    cellSize = 22; // Для средних экранов
  }
  else if (width < 600) {
    cellSize = 28; // Для средних экранов
  } 
  else if (width < 768) {
    cellSize = 34; // Для средних экранов
  } 
  else {
    cellSize = 40; // Для больших экранов
  }

  return {
    gridTemplateColumns: `repeat(${this.cols}, ${cellSize}px)`,
    gridTemplateRows: `repeat(${this.rows}, ${cellSize}px)`,
  };
    }
  },
  created() {
    this.difficulty = this.$route.query.difficulty || 'easy'; // Получаем уровень сложности из параметров
    this.rows = parseInt(this.$route.query.rows) || 8; // Получаем количество строк
    this.cols = parseInt(this.$route.query.cols) || 8; // Получаем количество столбцов
    this.initializeGame(); // Инициализация игры
  },
  methods: {    
    updateWindowWidth() {
    this.windowWidth = window.innerWidth; // Обновляем ширину окна
  },
   // Метод для открытия всех ячеек
   revealAllCells() {
    this.cells.forEach(cell => {
      cell.isOpened = true; // Открываем каждую ячейку
    });
  },
    setDifficulty(difficulty) {
      switch (difficulty) {
        case 'easy':
          this.rows = 8;
          this.cols = 8;
          this.remainingMines = 10;
          break;
        case 'medium':
          this.rows = 16;
          this.cols = 16;
          this.remainingMines = 40;
          break;
        case 'hard':
          this.rows = 32;
          this.cols = 16;
          this.remainingMines = 100;
          break;
        default:
          this.rows = 8;
          this.cols = 8;
          this.remainingMines = 10;
      }
    },
    startTimer() {
  // Убедитесь, что таймер не запущен
  if (!this.timerInterval) {
    this.timerInterval = setInterval(() => {
      this.timer++;
    }, 1000);}
    },
      initializeGame() {
  // Сбрасываем таймер на 0
  this.timer = 0; 
  // Инициализируем массив ячеек
  this.cells = []; 
  // Устанавливаем уровень сложности из параметров
  this.setDifficulty(this.difficulty); 
  // Запускаем таймер
  this.startTimer(); 

  // Заполняем массив ячеек, создавая ячейки с начальными значениями
  for (let i = 0; i < this.rows * this.cols; i++) {
    this.cells.push({ 
      id: i,            // Уникальный идентификатор ячейки
      isOpened: false,  // Ячейка еще не открыта
      isMine: false,    // Ячейка не содержит мину
      value: 0,         // Значение ячейки (количество соседних мин)
      flagged: null     // Флаг для обозначения, помечена ли ячейка
    });
  }

  // Размещаем мины на игровом поле
  this.placeMines(); 
  // Обновляем значения ячеек в зависимости от расположения мин
  this.updateCellValues(); 
},
placeMines() {
  let minesPlaced = 0; // Счетчик размещенных мин

  // Проверяем, сколько доступных ячеек (без мин) на поле
  const availableCells = this.cells.filter(cell => !cell.isMine).length;

  // Если доступных ячеек меньше, чем оставшихся мин, выходим из функции
  if (availableCells < this.remainingMines) {
    console.error('Недостаточно места для размещения мин!'); // Выводим сообщение об ошибке в консоль
    return; // Завершаем выполнение функции
  }

  // Размещаем мины случайным образом, пока не будет размещено нужное количество
  while (minesPlaced < this.remainingMines) {
    const randomIndex = Math.floor(Math.random() * this.cells.length); // Генерируем случайный индекс
    // Проверяем, не содержит ли ячейка мину
    if (!this.cells[randomIndex].isMine) {
      this.cells[randomIndex].isMine = true; // Размещаем мину в ячейке
      minesPlaced++; // Увеличиваем счетчик размещенных мин
    }
  }
},
handleCellClick(cell) {
  // Проверяем, открыта ли ячейка или помечена флагом; если да, то выходим из функции
  if (cell.isOpened || cell.flagged) return; 

  // Открываем ячейку
  cell.isOpened = true;

  // Проверяем, является ли ячейка миной
  if (cell.isMine) {
    alert('Игра окончена! Вы попали на мину!'); // Сообщаем о попадании на мину
    this.revealMines(); // Открываем все мины на поле   
    this.revealAllCells(); 
    this.timer = undefined; 
    return; // Завершаем выполнение функции
  }

  // Считаем количество мин вокруг текущей ячейки
  const mineCount = this.countAdjacentMines(cell.id);
  cell.value = mineCount; // Устанавливаем значение ячейки (количество соседних мин)

  // Если вокруг нет мин, открываем соседние клетки
  if (mineCount === 0) {
    this.openAdjacentCells(cell.id); // Открываем соседние клетки, если мин нет
  }

  // Проверяем, выиграл ли игрок
  this.checkForWin();
},
handleRightClick(cell) {
  // Проверяем, открыта ли ячейка; если да, то выходим из функции
  if (cell.isOpened) return; // Не меняем метки на открытых клетках

  // Циклическая смена меток: флажок, вопросик, без метки
  if (cell.flagged === null) {
    cell.flagged = 'flag'; // Устанавливаем флажок
    this.remainingMines--; // Уменьшаем количество оставшихся мин
  } else if (cell.flagged === 'flag') {
    cell.flagged = 'question'; // Меняем флажок на вопросик
    this.remainingMines++;
  } else {
    cell.flagged = null; // Снимаем метку    
  }

  // Проверяем условия победы после изменения метки
  this.checkForWin(); 
},
getAdjacentCells(cellId) {
  // Вычисляем строку и столбец текущей ячейки на основе её идентификатора
  const row = Math.floor(cellId / this.cols);
  const col = cellId % this.cols;
  const adjacentCells = []; // Массив для хранения соседних клеток

  // Проходим по всем соседним ячейкам (включая диагональные)
  for (let r = row - 1; r <= row + 1; r++) {
    for (let c = col - 1; c <= col + 1; c++) {
      // Проверяем, находится ли соседняя ячейка в пределах поля
      if (r >= 0 && r < this.rows && c >= 0 && c < this.cols) {
        const adjacentId = r * this.cols + c; // Вычисляем идентификатор соседней ячейки
        if (adjacentId !== cellId) { // Убедимся, что не добавляем саму ячейку
          adjacentCells.push(this.cells[adjacentId]); // Добавляем соседнюю ячейку в массив
        }                
      }
    }
  }
  return adjacentCells; // Возвращаем массив соседних клеток
},
countAdjacentMines(cellId) {
  // Получаем массив соседних клеток для данной ячейки
  const adjacentCells = this.getAdjacentCells(cellId);
  let mineCount = 0; // Переменная для подсчета количества мин

  // Проходим по всем соседним клеткам
  adjacentCells.forEach((cell) => {
    // Проверяем, является ли соседняя клетка миной
    if (cell.isMine) {
      mineCount++; // Увеличиваем счетчик мин, если клетка содержит мину
    }
  });

  return mineCount; // Возвращаем количество мин
},
openAdjacentCells(cellId) {
  // Получаем массив соседних клеток для данной ячейки
  const adjacentCells = this.getAdjacentCells(cellId);
  
  // Проходим по всем соседним клеткам
  adjacentCells.forEach(adjCell => {
    // Проверяем, открыта ли клетка и не установлен ли на ней флаг
    if (!adjCell.isOpened && !adjCell.flagged) {
      adjCell.isOpened = true; // Открываем клетку
      
      // Если клетка не содержит мину
      if (!adjCell.isMine) {
        // Подсчитываем количество мин вокруг соседней клетки
        const mineCount = this.countAdjacentMines(adjCell.id);
        adjCell.value = mineCount; // Устанавливаем значение клетки равным количеству мин

        // Если вокруг клетки нет мин, рекурсивно открываем соседние клетки
        if (mineCount === 0) {
          this.openAdjacentCells(adjCell.id); // Рекурсивный вызов для открытия соседних клеток
        }
      }
    }
  });
},
revealMines() {
  // Проходим по всем клеткам на поле
  this.cells.forEach((cell) => {
    // Проверяем, является ли клетка миной
    if (cell.isMine) {
      cell.isOpened = true; // Открываем клетку, если она содержит мину
    }
  });
},
checkForWin() {
  // Проверяем, открыты ли все клетки или установлены флаги на всех минах
  const allCellsOpenedOrFlagged = this.cells.every(cell => cell.isOpened || (cell.flagged === 'flag'));
  
  // Условие выигрыша: все клетки открыты или отмечены флагами, и не осталось мин
  if (allCellsOpenedOrFlagged && this.remainingMines === 0) {
    // Запрашиваем имя игрока после выигрыша
    const playerName = prompt('Поздравляем! Вы выиграли! 🎉 Введите ваше имя:');

    if (playerName) {
      const playerTime = this.timer; // Время, затраченное на игру
      const playerLevel = this.difficulty; // Уровень сложности
      const store = useLeaderboardStore(); // Инициализируем хранилище для таблицы лидеров

      // Проверяем, существует ли метод addPlayer в хранилище
      if (store && typeof store.addPlayer === 'function') {
        // Добавляем игрока в таблицу лидеров
        store.addPlayer({ name: playerName, time: playerTime, level: playerLevel });
        alert(`Спасибо за игру, ${playerName}! Ваше время: ${playerTime} секунд на уровне ${playerLevel}.`);
      } else {
        console.error('Ошибка: метод addPlayer не найден в хранилище.'); // Логируем ошибку, если метод не найден
      }
    } else {
      alert('Вы не ввели имя. Игра не сохранена.'); // Уведомляем, если имя не введено      
    }

    clearInterval(this.timerInterval); // Остановка таймера после выигрыша
  }
},

  
    restartGame() {      
      this.timer = 0;
      this.startTimer();
      this.initializeGame(); // Перезапуск игры
    },
    goToSettings() {
      this.$router.push('/settings'); // Переход к настройкам
    },
    getNumberClass(value) {
      switch (value) {
        case 1: return 'number-1'; // Синий
        case 2: return 'number-2'; // Зеленый
        case 3: return 'number-3'; // Красный
        case 4: return 'number-4'; // Тёмно-синий
        case 5: return 'number-5'; // Коричневый
        case 6: return 'number-6'; // Бирюзовый
        case 7: return 'number-7'; // Чёрный
        case 8: return 'number-8'; // Белый
        default: return '';
      }
    },
    updateCellValues() {
  // Проходим по каждой клетке в массиве cells
  this.cells.forEach(cell => {
    // Проверяем, не является ли клетка миной
    if (!cell.isMine) {
      // Обновляем значение клетки, подсчитывая количество соседних мин
      cell.value = this.countAdjacentMines(cell.id); // Обновляем значение клетки
    }
  });
},
    changeDifficulty(newDifficulty) {
      this.difficulty = newDifficulty; // Обновляем уровень сложности
      this.restartGame(); // Перезапускаем игру с новыми параметрами
    }
  },
  mounted() {
    this.updateWindowWidth(); // Устанавливаем начальную ширину окна
    window.addEventListener('resize', this.updateWindowWidth); // Обработчик изменения размера окна
    this.initializeGame(); // Инициализация игры при монтировании
  },

  beforeUnmount() { 
    clearInterval(this.timerInterval);
    window.removeEventListener('resize', this.updateWindowWidth); // Удаляем обработчик при уничтожении компонента       
  },
};
</script>

<style scoped>
